'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Skeleton } from '@/components/ui/skeleton';
import OpportunityCard from './opportunity-card';
import { toast } from 'sonner';
import { Search, Plus, Filter } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { Opportunity, OpportunitiesResponse, OpportunityFilters, OpportunityFilterParams } from '@/services/opportunity-service';

// Remove the duplicate interface definitions since we're importing them now

export default function OpportunityGrid() {
  const router = useRouter();
  const [opportunities, setOpportunities] = useState<Opportunity[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [stageFilter, setStageFilter] = useState('all');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [totalOpportunities, setTotalOpportunities] = useState(0);

  useEffect(() => {
    fetchOpportunities();
  }, [currentPage, stageFilter]);

  const fetchOpportunities = async () => {
    try {
      setLoading(true);
      
      // Create filters object
      const filterParams: OpportunityFilterParams = {
        page: currentPage,
        limit: 10,
        stage: stageFilter !== 'all' ? stageFilter : undefined,
        search: searchTerm || undefined
      };
      
      // Import and use the OpportunityService
      const { OpportunityService } = await import('@/services/opportunity-service');
      
      // Call the service with filter params
      const result = await OpportunityService.getOpportunities(filterParams);
      
      // Update state with the response data
      setOpportunities(result.opportunities || []);
      setTotalPages(Math.ceil((result.total || 0) / 10));
      setTotalOpportunities(result.total || 0);
    } catch (error) {
      console.error('Error fetching opportunities:', error);
      toast.error('Failed to load opportunities');
      // Set default values in case of error
      setOpportunities([]);
      setTotalPages(1);
      setTotalOpportunities(0);
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = () => {
    setCurrentPage(1);
    fetchOpportunities();
  };

  const handleStageChange = (value: string) => {
    setStageFilter(value);
    setCurrentPage(1);
  };

  const handleDeleteOpportunity = (id: string) => {
    setOpportunities(opportunities.filter(opportunity => opportunity.id !== id));
  };

  const renderSkeletons = () => {
    return Array(8).fill(0).map((_, index) => (
      <div key={index} className="h-full">
        <div className="h-full border rounded-lg p-4 space-y-3">
          <div className="flex justify-between items-start">
            <div>
              <Skeleton className="h-5 w-[150px] mb-1" />
              <Skeleton className="h-4 w-[100px]" />
            </div>
            <Skeleton className="h-8 w-8 rounded-full" />
          </div>
          <div className="space-y-2 pt-2">
            <div className="flex justify-between">
              <Skeleton className="h-4 w-[80px]" />
              <Skeleton className="h-4 w-[60px]" />
            </div>
            <div className="flex justify-between">
              <Skeleton className="h-4 w-[80px]" />
              <Skeleton className="h-4 w-[40px]" />
            </div>
            <div className="flex justify-between">
              <Skeleton className="h-4 w-[80px]" />
              <Skeleton className="h-4 w-[80px]" />
            </div>
          </div>
          <div className="pt-2 flex justify-between">
            <Skeleton className="h-6 w-[80px] rounded-full" />
            <Skeleton className="h-6 w-[60px]" />
          </div>
        </div>
      </div>
    ));
  };

  return (
    <div className="space-y-4">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div className="flex flex-col sm:flex-row gap-2 w-full sm:w-auto">
          <div className="relative w-full sm:w-[300px]">
            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
            <Input
              type="search"
              placeholder="Search opportunities..."
              className="pl-8 w-full"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && handleSearch()}
            />
          </div>
          <Button onClick={handleSearch} className="w-full sm:w-auto">
            Search
          </Button>
        </div>

        <div className="flex flex-col sm:flex-row gap-2 w-full sm:w-auto">
          <div className="flex items-center gap-2 w-full sm:w-auto">
            <Filter className="h-4 w-4 text-muted-foreground" />
            <Select value={stageFilter} onValueChange={handleStageChange}>
              <SelectTrigger className="w-full sm:w-[180px]">
                <SelectValue placeholder="Filter by stage" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Stages</SelectItem>
                <SelectItem value="prospecting">Prospecting</SelectItem>
                <SelectItem value="qualification">Qualification</SelectItem>
                <SelectItem value="proposal">Proposal</SelectItem>
                <SelectItem value="negotiation">Negotiation</SelectItem>
                <SelectItem value="closedWon">Closed Won</SelectItem>
                <SelectItem value="closedLost">Closed Lost</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <Button onClick={() => router.push('/dashboard/opportunities/create')} className="w-full sm:w-auto">
            <Plus className="h-4 w-4 mr-2" />
            New Opportunity
          </Button>
        </div>
      </div>

      {loading ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
          {renderSkeletons()}
        </div>
      ) : opportunities && opportunities.length === 0 ? (
        <div className="text-center py-10 border rounded-lg">
          <h3 className="text-lg font-medium">No opportunities found</h3>
          <p className="text-muted-foreground mt-1">
            {searchTerm || stageFilter !== 'all'
              ? 'Try adjusting your search or filters'
              : 'Create your first opportunity to get started'}
          </p>
          <Button 
            onClick={() => router.push('/dashboard/opportunities/create')} 
            className="mt-4"
          >
            <Plus className="h-4 w-4 mr-2" />
            New Opportunity
          </Button>
        </div>
      ) : (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
            {opportunities && opportunities.map((opportunity) => (
              <OpportunityCard
                key={opportunity.id}
                opportunity={opportunity}
                onDelete={handleDeleteOpportunity}
                onRefresh={fetchOpportunities}
              />
            ))}
          </div>

          <div className="flex justify-between items-center mt-6">
            <div className="text-sm text-muted-foreground">
              Showing {opportunities ? opportunities.length : 0} of {totalOpportunities} opportunities
            </div>
            <div className="flex gap-2">
              <Button
                variant="outline"
                onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
                disabled={currentPage === 1}
              >
                Previous
              </Button>
              <Button
                variant="outline"
                onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
                disabled={currentPage === totalPages}
              >
                Next
              </Button>
            </div>
          </div>
        </>
      )}
    </div>
  );
}